/* eslint-disable */
import React, { useState, useEffect } from "react";
import db from "../data/dbStore";
import { Capacitor, Geolocation, GeolocationPosition } from "@capacitor/core";
import { log } from "../util/logger";

import PushController from "../util/push-notifications-controller";
import env from "../api/environment";

export interface ApplicationUser {
  isLoggedin: boolean;
  access_token?: string;
  username?: string;
  darkMode: boolean;
  loading: boolean;
  latitude?: number;
  longitude?: number;
  lastLocationUpdate?: Date;
  environment: string;
  isPushEnabled: boolean;
  requestedGeoPermission: boolean;
  refusedGeoPermission: boolean;
}

export interface IApplicationUserContext {
  user: ApplicationUser;
  logout: () => Promise<void>;
  userLoggedIn: (
    username: string,
    access_token: string,
    environment: string
  ) => Promise<ApplicationUser>;
  startWatchPosition: (
    callback: (lat?: number, lng?: number, err?: string) => void
  ) => string;
  clearWatchPosition: (watchId: string) => void;
  pushEnabled: (enabled: boolean) => void;
  requestGeoPermission: () => void;
  setRefusedGeoPermission: (refused: boolean) => void;
}

// Possible values for code, https://developer.mozilla.org/en-US/docs/Web/API/GeolocationPositionError
// 1: PERMISSION_DENIED
// 2: POSITION_UNAVAILABLE
// 3: TIMEOUT
interface GeolocationPositionError {
  code: number;
}

const INITIAL_APPLICATION_USER: ApplicationUser = {
  isLoggedin: false,
  access_token: undefined,
  username: "",
  darkMode: false,
  loading: true,
  latitude: undefined,
  longitude: undefined,
  lastLocationUpdate: undefined,
  environment: "prod",
  isPushEnabled: false,
  requestedGeoPermission: false,
  refusedGeoPermission: false,
};

const geoPositionOptions = {
  enableHighAccuracy: true,
  maximumAge: 1000,
};

const INITIAL_APPLICATION_USER_CONTEXT = {
  user: INITIAL_APPLICATION_USER,
  logout: async () => {},
  userLoggedIn: (u: string, at: string, e: string) => {
    return new Promise<ApplicationUser>(() => {
      return INITIAL_APPLICATION_USER;
    });
  },
  startWatchPosition: (callback: (lat: number, lng: number) => void) => {
    return "";
  },
  clearWatchPosition: (s: string) => {},
  pushEnabled: (e: boolean) => {},
  requestGeoPermission: () => {},
  setRefusedGeoPermission: (refused: boolean) => {},
};

export const ApplicationUserContext = React.createContext<
  IApplicationUserContext
>(INITIAL_APPLICATION_USER_CONTEXT);

export const ApplicationUserContextProvider = (props: any) => {
  const [userState, internalSetUserState] = useState({
    ...INITIAL_APPLICATION_USER,
  });

  // ----------------------------------------------------------------------------------------------
  function startWatchPosition(
    callback: (lat?: number, lng?: number, err?: string) => void
  ): string {
    const watchPositionCallBack = (position: GeolocationPosition) => {
      if (!position || !position.coords) {
        return;
      }

      let reason = "unknown";
      let positionChanged: boolean;
      if (
        google &&
        google.maps &&
        google.maps.geometry &&
        google.maps.geometry.spherical &&
        google.maps.geometry.spherical.computeDistanceBetween &&
        userState.latitude &&
        userState.longitude
      ) {
        if (userState.latitude && userState.longitude) {
          const oldPos = new google.maps.LatLng(
            userState.latitude,
            userState.longitude
          );
          const newPos = new google.maps.LatLng(
            position.coords.latitude,
            position.coords.longitude
          );
          const distance =
            google.maps.geometry.spherical.computeDistanceBetween(
              oldPos,
              newPos
            ) * 3.28084;
          positionChanged = distance > 2.0; // we've change donly if distance is greater than 3 feet
          reason = "distance greater than 2 feet (" + distance + ")";
        } else {
          positionChanged = true;
          reason = "original lat/lng were empty";
        }
      } else {
        positionChanged =
          userState.latitude !== position.coords.latitude ||
          userState.longitude !== position.coords.longitude;
        reason = "position different";
      }

      if (positionChanged) {
        console.log(
          "Position changed, calling callback and setting user state, " + reason
        );
        const newState = {
          ...userState,
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
          lastLocationUpdate: new Date(),
        };
        internalSetUserState(newState);
        callback(position.coords.latitude, position.coords.longitude);
      }
    };

    const errCallback = (err: GeolocationPositionError) => {
      console.log("Could not get geolocation, error code " + err.code);
      if (err.code === 1) {
        // permission denied
        callback(
          undefined,
          undefined,
          "Could not get location: permission denied"
        );
      }
    };

    let watchId = "";
    if (
      env.settings.hasCapacitor &&
      Capacitor.isPluginAvailable("Geolocation")
    ) {
      console.log("ApplicatinUserContext: Using Cap plugin to watch position");
      try {
        watchId = Geolocation.watchPosition(
          geoPositionOptions,
          watchPositionCallBack
        );
      } catch (err) {
        errCallback({ code: 1 });
      }
    } else if (navigator && navigator.geolocation) {
      console.log("ApplicatinUserContext: Using browser API");
      watchId = navigator.geolocation.watchPosition(
        // @ts-ignore  (watchPositionCallBack's GeolocationPosition param is wonky between Cap & Nav
        watchPositionCallBack,
        errCallback,
        geoPositionOptions
      );
    } else {
      console.log(
        "ApplicationUserContext.startWatchPosition: Geolocation unavailable via cap/nav"
      );
      return "";
    }

    console.log("ApplicationUserContext.startWatchPosition: start " + watchId);
    return watchId;
  }

  // ----------------------------------------------------------------------------------------------
  function clearWatchPosition(watchId: string) {
    if (watchId === "") {
      return;
    }

    if (
      env.settings.hasCapacitor &&
      Capacitor.isPluginAvailable("Geolocation")
    ) {
      Geolocation.clearWatch({ id: watchId }).then(() => {
        console.log(
          "ApplicationUserContext.stopWatchPosition: cleared " +
            watchId +
            " from Capacitor plugin"
        );
      });
    } else if (navigator && navigator.geolocation) {
      navigator.geolocation.clearWatch(parseInt(watchId));
      console.log(
        "ApplicationUserContext.stopWatchPosition: cleared browser API watchId " +
          watchId
      );
    }
  }

  // ----------------------------------------------------------------------------------------------
  const requestGeoPermission = async () => {
    if (userState.requestedGeoPermission) {
      return;
    }

    try {
      if (
        env.settings.hasCapacitor &&
        Capacitor.isPluginAvailable("Geolocation")
      ) {
        console.log(
          "ApplicationUserContext: request geolocation permission via cap"
        );
        await Geolocation.getCurrentPosition(geoPositionOptions);
      } else if (navigator && navigator.geolocation) {
        console.log(
          "ApplicationUserContext: request geolocation permission via nav"
        );
        await navigator.geolocation.getCurrentPosition((pos) => {});
      } else {
        console.log("Could not request geolocation through Cap or nav");
      }

      const newState = {
        ...userState,
        latitude: undefined,
        longitude: undefined,
        lastLocationUpdate: new Date(),
        requestedGeoPermission: true,
      };

      await db.putUserState(newState);
      internalSetUserState(newState);
      console.log("ApplicationUserContext: RequestedGeoPermission set to true");
    } catch (err) {
      const newState = {
        ...userState,
        latitude: undefined,
        longitude: undefined,
        lastLocationUpdate: new Date(),
        requestedGeoPermission: true,
      };

      await db.putUserState(newState);
      internalSetUserState(newState);
      console.log("ApplicationUserContext: RequestedGeoPermission set to true");
    }
  };

  // ----------------------------------------------------------------------------------------------
  const setRefusedGeoPermission = async (refused: boolean) => {
    const newState = {
      ...userState,
      refusedGeoPermission: refused,
    };

    await db.putUserState(newState);
    internalSetUserState(newState);
  };

  // ----------------------------------------------------------------------------------------------
  useEffect(() => {
    let mounted = true;

    Promise.all([db.getUserState()]).then((response) => {
      if (mounted) {
        const newState = response[0];
        if (newState) {
          internalSetUserState({ ...newState, loading: false });
        } else {
          internalSetUserState({ ...userState, loading: false });
        }
      }
    });
    return () => {
      mounted = false;
    };

    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (
      userState &&
      !userState.isPushEnabled &&
      userState.access_token &&
      userState.access_token.length > 0 &&
      env.settings.platform !== "other"
    ) {
      PushController.register(userState, pushEnabled, () => {});
    }

    // eslint-disable-next-line
  }, [userState.isPushEnabled, userState.access_token]);

  // ----------------------------------------------------------------------------------------------
  const pushEnabled = (enabled: boolean) => {
    internalSetUserState({ ...userState, isPushEnabled: enabled });
  };

  // ----------------------------------------------------------------------------------------------
  const userLoggedIn = async (
    username: string,
    access_token: string,
    environment: string
  ): Promise<ApplicationUser> => {
    const currentState = await db.getUserState();
    const newState = {
      username,
      access_token,
      environment,
      isLoggedin: true,
      darkMode: currentState
        ? currentState.darkMode
        : INITIAL_APPLICATION_USER.darkMode,
      loading: false,
      latitude: undefined,
      longitude: undefined,
      isPushEnabled: false,
      requestedGeoPermission: false,
      refusedGeoPermission: false,
    };

    await db.putUserState(newState);
    internalSetUserState(newState);
    return newState;
  };

  // ----------------------------------------------------------------------------------------------
  const logout = async () => {
    const newState = {
      ...userState,
      isLoggedin: false,
      access_token: undefined,
      latitude: undefined,
      longitude: undefined,
      isPushEnabled: false,
      requestedGeoPermission: false,
      refusedGeoPermission: false,
    };
    internalSetUserState(newState);
    await db.putUserState(newState);
  };

  return (
    <ApplicationUserContext.Provider
      value={{
        user: userState,
        userLoggedIn,
        logout,
        startWatchPosition,
        clearWatchPosition,
        pushEnabled,
        requestGeoPermission,
        setRefusedGeoPermission,
      }}
    >
      {props.children}
    </ApplicationUserContext.Provider>
  );
};
