import React, { useEffect, useState } from "react";
// import "./ClockPanel.scss";
import "../../global-styles.scss";
import { ApplicationUserContext } from "../../context/ApplicationUserContext";
import { AuthorizedItemsContext } from "../../context/AuthorizedItemsContext";
import { log } from "../../util/logger";
import authHelper from "../../util/authitems-helper";
import Map from "./Map";
import useYesOrCancelDialogBox from "../../hooks/useYesOrCancelDialogBox";
import useSimpleToast from "../../hooks/useSimpleToast";

interface GeoLocationComponentProps {
  updateCallback: (lat?: number | undefined, lng?: number | undefined) => void;
  isShowing: boolean;
}

const GeoLocationComponent: React.FC<GeoLocationComponentProps> = ({
  updateCallback,
  isShowing,
}: GeoLocationComponentProps) => {
  const {
    user,
    startWatchPosition,
    clearWatchPosition,
    requestGeoPermission,
    setRefusedGeoPermission,
  } = React.useContext(ApplicationUserContext);
  const { auth } = React.useContext(AuthorizedItemsContext);
  const yesCancelDlg = useYesOrCancelDialogBox();
  const [watchPositionId, setWatchPositionId] = useState("");
  const toast = useSimpleToast();

  // ------------------------------------------------------------------------------------
  useEffect(() => {
    if (isShowing) {
      if (
        auth &&
        auth.locations &&
        auth.locations.length > 0 &&
        authHelper.requiresGeolocation(auth.locations)
      ) {
        if (!user.requestedGeoPermission && !user.refusedGeoPermission) {
          console.log("GeoLocationComponent: Prompting user");
          yesCancelDlg.showDialog(
            "To clock in/out, you may be required to provide your location. " +
              "Do you want Team Dashboard to send location data?",
            () => {
              requestGeoPermission();
            },
            () => {
              setRefusedGeoPermission(true);
            }
          );
        } else if (user.requestedGeoPermission && !user.refusedGeoPermission) {
          const watchId = startWatchPosition((lat, lng, err) => {
            log("geolocation_updated", { user: user.username });

            if (lat === undefined || lng === undefined) {
              return;
            }

            if (err && err.length > 0) {
              log("geolocation error: " + err);
              console.log("geolocation error: " + err);
              toast.showToast("Cannot read geolocation data");
              clearWatchPosition(watchId);
            } else {
              setTimeout(() => {
                updateCallback(lat, lng);
              }, 150);
            }
          });

          setWatchPositionId(watchId);
        }
      }
    } else {
      if (
        auth &&
        auth.locations &&
        auth.locations.length > 0 &&
        authHelper.requiresGeolocation(auth.locations) &&
        watchPositionId !== ""
      ) {
        console.log("GeoLocationComponent: stopping watch position");
        clearWatchPosition(watchPositionId);
        setWatchPositionId("");
      }
    }
    // eslint-disable-next-line
  }, [
    isShowing,
    auth,
    auth.locations,
    user.requestedGeoPermission,
    user.refusedGeoPermission,
  ]);

  return (
    <>
      {auth &&
        auth.locations &&
        auth.locations.length > 0 &&
        authHelper.requiresGeolocation(auth.locations) && <Map />}
      {yesCancelDlg.dialogJsx}
      {toast.simpleToastJsx}
    </>
  );
};

export default GeoLocationComponent;
